import React from 'react';
import Questionnaire from '../../Questionnaire';
import { useNavigate } from "react-router-dom";

const questions = [
    {
        question: 'What should be the type of the variable defining the name of the planet?',
        options: ['int', 'float', 'string', 'bool'],
        answer: 'string',
        explanation: {
            'int': 'The type int is used for integer values, but a name requires text.',
            'float': 'The type float is used for decimal numbers, but a name requires text.',
            'string': 'Correct. A name should be represented as a string.',
            'bool': 'The type bool is used for true/false values, but a name requires text.'
        }
    },
    {
        question: 'What should be the type of the variable defining the velocity?',
        options: ['int', 'float', 'string', 'bool'],
        answer: 'float',
        explanation: {
            'int': 'The type int is used for integer values, but velocity often requires decimal precision.',
            'float': 'Correct. Velocity should be represented as a float for precision.',
            'string': 'The type string is used for text, not numerical values.',
            'bool': 'The type bool is used for true/false values, not numerical values.'
        }
    },
    {
        question: 'What is the correct type for a variable representing the number of moons?',
        options: ['int', 'float', 'string', 'bool'],
        answer: 'int',
        explanation: {
            'int': 'Correct. The number of moons is a whole number.',
            'float': 'The type float is used for decimal numbers, but the number of moons is a whole number.',
            'string': 'The type string is used for text, not numerical values.',
            'bool': 'The type bool is used for true/false values, not numerical values.'
        }
    },
    {
        question: 'Which type would be best for a variable indicating if a planet has rings?',
        options: ['int', 'float', 'string', 'bool'],
        answer: 'bool',
        explanation: {
            'int': 'The type int is used for integer values, but a true/false question is better represented by a boolean.',
            'float': 'The type float is used for decimal numbers, but a true/false question is better represented by a boolean.',
            'string': 'The type string is used for text, but a true/false question is better represented by a boolean.',
            'bool': 'Correct. A boolean is ideal for true/false questions.'
        }
    }
];

const Lecture1Questionnaire: React.FC = () => {
    const navigate = useNavigate();

    const handleSuccess = () => {
        const completionStatus = JSON.parse(localStorage.getItem('completionStatus') || '{}');
        completionStatus['lecture1Questionnaire'] = true;
        localStorage.setItem('completionStatus', JSON.stringify(completionStatus));
        navigate('/week1/lecture2');
    };

    return <Questionnaire questions={questions} onSuccess={handleSuccess} />;
};

export default Lecture1Questionnaire;
